import Vue from 'vue';
import App from './App';
import './assets/fullpage/jquery.fullpage.min.css';
import './assets/fullpage/jquery.fullpage.min.js';
import './common/less/font.less';

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App }
});



