              color: #4985b4;
              font-weight: bold;
              font-size: 18px;
            }
            .text{
              padding: 30px 0 0 20px;
              color: #000;
              font-size: 16px;
            }
            .link{
              display: block;
              margin: 30px 0 0 20px;
              width: 180px;
              height: 30px;
              text-align: center;
              border-radius: 5px;
              line-height: 30px;
              border: 1px solid rgba(7,17,27,0.1);
              color: #e6e6e6;
              &:hover{
                cursor: pointer;
                animation: linkFade 2s;
                @keyframes linkFade {
                  0% {color: #e6e6e6;}
                  100% {color: #000;background: #e6e6e6;opacity: 0.8;}
                }
              }
            }
          }
        }
        .github{
          width: 80%;
          margin: 80px auto 0 auto;
          text-align: center;
          a{
            color: #fff;
          }
          @media screen and (max-width: 500px) {
            margin: 20px auto 0 auto;
          }
        }
        .button-wrapper{
          width: 80%;
          margin: 30px auto 0 auto;
          display: none;
          @media screen and (max-width: 500px) {
            display: block;
          }
          .pre,.next{
            font-size: 30px;
            color: #fff;
          }
          .pre{
            float: left;
          }
          .next{
            float: right;
          }
        }
      }
      .section6{
        background: #9c946d;
        .title{
          padding-top: 100px;
          text-align: center;
          color: #fff;
          font-size: 26px;
        }
        .text-wrapper{
          width: 60%;
          margin: 20px auto 0 auto;
          text-align: center;
          .contact-item{
            display: inline-block;
            font-size: 22px;
            margin: 30px 40px 0 0;
            &:last-child{
              margin-right: 0;
            }
            &:nth-of-type(2),&:last-child{
              @media screen and (max-width: 386px) {
                margin-right: 20px;
              }
            }
            &.one{
              color: #ef0039;
            }
            &.two{
              color: #33ccff;
            }
            &.three{
              color: #96bc26;
            }
            &.four{
              color: #c89561;
            }
          }
          .text{
            margin-top: 30px;
            line-height: 40px;
            color: #fff;
            font-size: 18px;
          }
          .icon{
            display: inline-block;
            margin: 50px 30px 0 0;
            border: 1px solid #fff;
            border-radius: 50%;
            width: 42px;
            height: 42px;
            text-align: center;
            line-height: 42px;
            color: #fff;
            font-size: 22px;
            cursor: pointer;
            &:last-child{
              margin-right: 0;
            }
          }
        }
        .bottom{
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 50px;
          border-top: 1px solid rgba(7,17,27,0.1);
          .text{
            color: rgba(7,17,27,0.3);
            font-size: 14px;
            text-align: center;
            line-height: 50px;
          }
        }
      }
    }
  }

</style>



